/* ----------------------------------------------------------------------------
About: A hexmap showing the results of the 2015 local elections for councils
electing in the 2019 local elections.
Author: Oliver Hawkins
*/

/* Imports ----------------------------------------------------------------- */

import "whatwg-fetch";
import * as d3 from "d3";
import * as d3_hexjson from "d3-hexjson";

/* Imports ----------------------------------------------------------------- */

function draw(hexjson) {

    // State
    let activeGroup = null;
    let activeHex = null;

    // Sizes and margins
    const margin = {top: 2, right: 2, bottom: 2, left: 2},
        width = 700 - margin.left - margin.right,
        height = 650 - margin.top - margin.bottom,
        viewBox = "0 0 " + 700 + " " + 650;

    // Render hexes
    const hexes = d3_hexjson.renderHexJSON(hexjson, width, height);
    const datahexes = d3_hexjson.renderHexJSON(hexjson, width, height);

    // Process councillor data
    hexes.forEach(hex => {
        let cumsum = 0;
        hex.elected.forEach(elected => {
            elected.councillors = +elected.councillors;
            elected.cumsum = cumsum;
            cumsum = cumsum + elected.councillors;
        });
        hex.councillors = cumsum;
    });

    datahexes.forEach(hex => {
        let cumsum = 0;
        hex.elected.forEach(elected => {
            elected.councillors = +elected.councillors;
            elected.cumsum = cumsum;
            cumsum = cumsum + elected.councillors;
        });
        hex.councillors = cumsum;
    });

    // Create the svg element
    const svg = d3
        .select("#vis-container")
        .append("svg")
        .attr("id", "vis")
        .attr("viewBox", viewBox)
        .append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    // Select the infobox
    const infobox = d3.select("#infobox");

    // Add background canvas for click events
    svg
        .append("rect")
        .attr("width", "100%")
        .attr("height", "100%")
        .attr("stroke", "#ffffff")
        .attr("stroke-width", 0)
        .attr("stroke-opacity", 0)
        .attr("fill", "#ffffff")
        .attr("fill-opacity", 0)
        .on("click", () => {
            if (activeGroup !== null && activeHex !== null) {
                activeGroup.attr("stroke-opacity", getOpacity(activeHex.party));
                activeGroup.attr("fill-opacity", getOpacity(activeHex.party));
                activeGroup.attr("fill", getColor(activeHex.party));
                hideInfoBox(infobox);
                activeGroup = null;
                activeHex = null;
            }
        });

    // Create and render the hexgrid
    const hexgrid = svg
        .selectAll("g.grid")
        .data(hexes)
        .enter()
        .append("g")
        .attr("transform", hex => "translate(" + hex.x + "," + hex.y + ")");

    hexgrid
        .append("polygon")
        .attr("points", hex => hex.points)
        .attr("stroke", "#b0b0b0")
        .attr("stroke-width", 1)
        .attr("fill", "#f0f0f0");

    // Create and render the hexmap
    const hexmap = svg
        .selectAll("g.data")
        .data(datahexes)
        .enter()
        .append("g")
        .attr("transform", hex => "translate(" + hex.x + "," + hex.y + ")");

    hexmap
        .append("polygon")
        .attr("points", hex => hex.points)
        .attr("stroke", "#ffffff")
        .attr("stroke-width", 2)
        .attr("stroke-opacity", hex => getOpacity(hex.party))
        .attr("fill-opacity", hex => getOpacity(hex.party))
        .attr("fill", hex => getColor(hex.party))
        .on("mouseover", (hex, i , n) => {
            const g = d3.select(n[i]);
            g.attr("stroke-opacity", 1);
            g.attr("fill-opacity", 1);
            g.attr("fill", "#80ffff");
            showInfoBox(infobox, hex);
            activeGroup = g;
            activeHex = hex;
        })
        .on("mouseout", (hex, i , n) => {
            const g = d3.select(n[i]);
            g.attr("stroke-opacity", getOpacity(hex.party));
            g.attr("fill-opacity", getOpacity(hex.party));
            g.attr("fill", getColor(hex.party));
            hideInfoBox(infobox);
            activeGroup = null;
            activeHex = null;
        });
}

/* Infobox ----------------------------------------------------------------- */

// Shows the infobox with the given data
function showInfoBox(infobox, hex) {

    const html = `
        <p class="infobox-label">${hex.name}</p>
        <p class="infobox-label" style="font-weight: bold; color: ${getColor(hex.party)};">
            ${hex.result}
        </p>
        <div id="infoboxbar"></div>`;

    // Set infobox content
    infobox.html(html);

    if (hex.party != "No elections") {

        // Sizes and margins for the bar svg
        const margin = {top: 2, right: 2, bottom: 2, left: 2};
        const width = 150 - margin.left - margin.right;
        const height = 20 - margin.top - margin.bottom;

        // Create the bar svg element
        const bar = d3.select("#infoboxbar")
            .append("svg")
            .attr("width", width + margin.left + margin.right)
            .attr("height", height + margin.top + margin.bottom)
            .append("g")
            .attr("transform", `translate(${margin.left},${margin.top})`);

        // Create the xscale
        const xscale = d3.scaleLinear()
            .domain([0, hex.councillors])
            .range([0, width]);

        // Create the yscale
        const yscale = d3.scaleLinear()
            .domain([0, 1])
            .range([height, 0]);

        // Append the rectangles
        bar.selectAll(".partyrects")
            .data(hex.elected)
            .enter()
            .append("rect")
            .attr("class", "partyrects")
            .attr("x", d => xscale(d.cumsum))
            .attr("y", () => yscale(1))
            .attr("width", d => xscale(d.councillors))
            .attr("height", height)
            .attr("fill", d => getColor(d.party));
    }

    // Draw infobox
    const box = infobox.node().getBoundingClientRect();
    const boxHeight = box.height;
    const boxWidth = box.width;

    let infoBoxPosX = d3.event.pageX,
        infoBoxPosY = d3.event.pageY - boxHeight;

    if (hex.q > 13) infoBoxPosX = d3.event.pageX - boxWidth;

    infobox
        .style("left", infoBoxPosX + "px")
        .style("top", infoBoxPosY + "px");

    infobox
        .transition()
        .duration(200)
        .style("opacity", .96);
}

// Hides the infobox
function hideInfoBox(infobox) {

    infobox
        .transition()
        .duration(500)
        .style("opacity", 0);
}

// Fill function
function getColor(party) {

    switch (party) {
    case "CON":
        return "#00539f";
    case "LAB":
        return "#d50000";
    case "LD":
        return "#faa01a";
    case "UKIP":
        return "#722889";
    case "GREEN":
        return "#78b82a";
    case "NOC":
        return "#909090";
    case "OTH":
        return "#404040";
    case "No elections":
        return "#909090";
    default:
        return "#404040";
    }
}

// Opacity function
function getOpacity(party) {
    return (party == "No elections") ? 0 : 1;
}

/* Container function ------------------------------------------------------ */

function sendHeight() {

    if (window.navigator.userAgent.indexOf("Trident") > 0) {
        document.getElementById("vis-container").style.height = "650px";
    }

    const height = document
        .getElementById("vis-container")
        .getBoundingClientRect().height;

    parent.postMessage(height, "*");
}

/* Global execution -------------------------------------------------------- */

d3.json("election-results-2019.json"). then((hexjson) => {

    draw(hexjson);
    sendHeight();
    window.addEventListener("resize", () => sendHeight());
    window.addEventListener("orientationchange", () => sendHeight());
});
